.table{
    margin-top: 20px;
    margin-bottom: 20px;
    overflow-y: scroll;
    height: 415px;
    color: #6a5d5d;
}

.table tr{
    display: flex;
    justify-content: space-between;
}

.table td{
    padding: 0.5rem;
}

.table tr:nth-of-type(odd){
    background-color: rgb(225, 225, 225);
}